*
{   box-sizing: border-box;
}

.card
{
    border: 2px solid blue;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    height: auto;
    overflow: none;
    padding: 5px;
    margin: 10px;
    width: 120px;
}

.card img
{   display: block;
    flex-grow: 1;
    height: auto;
    width: 100%;
}

.name-div
{   flex-grow: 0;
    text-align: center;
}
*
{   box-sizing: border-box;
}

.header-div
{   display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    font-family: "Fredoka One", cursive;
    font-size: 30px;
    justify-content: space-between;
    padding: 15px;
}

.title-div
{   flex-grow: 1;
    text-align: left;
    width: 300px;
}

.score-div
{   flex-grow: 0;
    padding: 5px;
    text-align: right;
}

@media screen and (max-width: 720px)
{
    .header-div
    {   flex-wrap: wrap;
        justify-content: center;
    }

    .title-div
    {   flex-grow: 1;
        text-align: center;
        width: 100%;
    }

    .score-div
    {   flex-grow: 0;
        text-align: center;
        width: 100%;
    }
}
.wrapper
{   
    /* display: flex; */
    /* flex-direction: row; */
    /* justify-content: space-between; */
    margin: 0px auto;
    width: 80vw;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

main
{   display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0px auto;
    width: 70vw;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

